<template>
  <div class="container-fluid">
    <div class="row">
      <nav class="navbar w-100">
        <svg
          @click="switchLeftBar"
          class="btn-menu"
          viewBox="0 0 32 32"
          width="32"
          height="32"
          fill="none"
          stroke="currentcolor"
          stroke-linecap="round"
          stroke-linejoin="round"
          stroke-width="2">
          <path d="M13 2 L13 6 11 7 8 4 4 8 7 11 6 13 2 13 2 19 6 19 7 21 4 24 8 28 11 25 13 26 13 30 19 30 19 26 21 25 24 28 28 24 25 21 26 19 30 19 30 13 26 13 25 11 28 8 24 4 21 7 19 6 19 2 Z"/>
          <circle
            cx="16"
            cy="16"
            r="4"/>
        </svg>
        <ul
          v-if="isLoggedIn"
          class="navbar-nav flex-row ml-md-auto d-md-flex">
          <li class="nav-item">
            <div class="m-2">Address: {{ address() }}</div>
          </li>
          <li class="nav-item">
            <div class="m-2">Balance: {{ balance }} XEM</div>
          </li>
          <li class="nav-item">
            <div class="m-2"> Network: {{ network }}</div>
          </li>
        </ul>
      </nav>
    </div>
    <div class="row">
      <div class="col-3 m-0 p-0 br">
        <component :is="leftBar"/>
      </div>
      <div class="col-9">
        <component :is="rightBar"/>
      </div>
    </div>
  </div>
</template>

<script>
// checked
import { mapActions, mapGetters } from 'vuex';
import DialogList from '@/components/DialogList';
import Menu from '@/components/Menu';
import ChatContainer from '@/components/ChatContainer';
import Login from '@/components/Login';
import Signup from '@/components/Signup';
import Nodes from '@/components/Nodes';

export default {
  name: 'Container',
  components: {
    DialogList,
    ChatContainer,
    Menu,
    Login,
    Signup,
    Nodes,
  },
  data() {
    return {};
  },
  methods: {
    ...mapActions([
      'setMsg',
      'setMe',
      'setLeftBar']),
    switchLeftBar() {
      const bar = this.leftBar === 'DialogList' ? 'Menu' : 'DialogList';
      this.setLeftBar(bar);
    },

  },
  computed: {
    ...mapGetters({
      rightBar: 'getRightBar',
      leftBar: 'getLeftBar',
      network: 'getNetwork',
      balance: 'getBalance',
      address: 'getMe',
      isLoggedIn: 'getLoggedIn',
    }),
  },
};
</script>

<style scoped>
.br {
  border-right-style: dashed;
  border-right-color: #6AACDE;
}
nav {
  color: #fff;
  background: #6AAEDF!important;
}
.btn-menu {
    cursor: pointer;
    color: #fdfeff!important;
    padding: 2px;
}
.btn-menu:hover {
    background: #6f9bbb!important;
    border-radius: 15px;
}
</style>



